import Client from './client'
import { Buffer } from 'Buffer'

class Server {
    constructor(responseListener) {
      this._responseListener = responseListener;
      this._client = null;
    }
    connect(url) {
      this._client = new Client(this._responseListener);
      this._client.connect(url);
      return this._client;
    }

    request(options) {
      if(this._client) {
        this._client.request(options)
      }
    }

    close() {
      if(this._client) {
        this._client.close()
      }
    }
}

function createClientServer(responseListener) {
    return new Server(responseListener);
}

let webStreamer = {
  createClientServer
};

export default webStreamer



